import React from "react";
import ElementLogo from "../element-logo/element-logo";
import Maps from "./maps/maps";
import List from "./list/list";
import "./interface.css";

function Interface() {
  return (
    <div className="interface">
      <div className="interface__vertical">
        <ElementLogo />
      </div>
      <div className="interface__horizontal">
        <div className="interface__horizontal__1"></div>
        <div className="interface__horizontal__2">
          <Maps />
        </div>
        <div className="interface__horizontal__3">
          <List />
        </div>
      </div>
    </div>
  );
}

export default Interface;
