import React from "react";
// import { YMaps, Map } from "react-yandex-maps";
import "./maps.css";

function Maps() {
  return (
    <div className="maps">
      <div id="map" className="maps__maps map"></div>
      <div className="maps__list">
        <div className="flex">
          <div className="maps_left">Servers</div>
          <div className="maps_right">(8 online of 10)</div>
        </div>
        <input className="search__ap" placeholder="Search AP" />
        <div className="block__server__list">
          <div class = "serv__list">
            <span className=" server">Name </span>
            <span className=" server">Uptime</span>
            <span className=" server">Connections</span>
          </div>
          <div className="server__list"></div>
        </div>
      </div>
    </div>
  );
}

export default Maps;
